import React, { Component } from 'react';
import './style.scss';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import md5 from 'md5';
import { FormControl, InputLabel, Input, FormHelperText } from '@material-ui/core';
import PageHeader from '../../components/PageHeader';
import { resetPasswordAction, resetPasswordData } from './action';
import Button from '../../components/Button';
import { generateRandomNumber } from '../../utilities/generateRandomNumber';

class ResetPassword extends Component {

    state = {
        fields: {
            login_id: null,
            new_password: "",
            confirm_new_password: "",
            sum: ""
        },
        errors: {
            login_id: null,
            new_password: "",
            confirm_new_password: "",
            sum: ""
        },
        enableSubmit: false
    }

    componentDidMount() {
        let { fields } = this.state;
        fields.login_id = this.props.login_id;
        this.setState({ fields });
        this.genarateNumbers();
    }

    componentWillReceiveProps(props) {
        if (props.resetData) {
            let fields = {
                login_id: props.login_id,
                new_password: "",
                confirm_new_password: "",
                sum: ""
            }
            this.setState({ fields, enableSubmit: false }, () => {
                this.genarateNumbers();
                this.props.resetPasswordData();
            });
        }
    }

    componentWillUnmount() {
        this.props.resetPasswordData();
    }

    genarateNumbers = () => {
        this.setState({
            number1: generateRandomNumber(0, 9),
            number2: generateRandomNumber(0, 9)
        });
    }

    checkValidityOfForm = () => {
        let { fields, errors } = this.state;
        let hasErrors = false, validForm = false;
        let requiredFields = ["new_password", "confirm_new_password", "sum"];
        validForm = requiredFields.every(field => fields[field] !== "");
        hasErrors = Object.keys(errors).length !== 0 && requiredFields.some(field => errors[field] && errors[field] !== "");
        this.setState({
            enableSubmit:
                !hasErrors &&
                validForm &&
                Number(this.state.number1) + Number(this.state.number2) === Number(this.state.fields.sum)
        });
    }

    handleChange = (event) => {
        let fieldName = event.target.name;
        let fieldValue = event.target.value;

        let { fields, errors } = this.state;

        switch (fieldName) {
            case "new_password":
                fields[fieldName] = fieldValue;
                if (fieldValue.length > 0) {
                    fields[fieldName] = fieldValue;
                    if (fields['confirm_new_password'] !== '' && fields['new_password'] !== fields['confirm_new_password']) {
                        errors[fieldName] = 'New password and Confirm password didn\'t match';
                    } else {
                        errors[fieldName] = '';
                        errors['confirm_new_password'] = '';
                    }
                } else if (fieldValue.length === 0) {
                    fields[fieldName] = fieldValue;
                    errors[fieldName] = 'New Password Should not be Empty';
                }
                break;


            case "confirm_new_password":
                fields[fieldName] = fieldValue;
                if (fieldValue.length > 0) {
                    fields[fieldName] = fieldValue;
                    if (fields['new_password'] !== '' && fields['confirm_new_password'] !== fields['new_password']) {
                        errors[fieldName] = 'New password and Confirm password didn\'t match';
                    } else {
                        errors[fieldName] = '';
                        errors['new_password'] = '';
                    }
                } else if (fieldValue.length === 0) {
                    fields[fieldName] = fieldValue;
                    errors[fieldName] = 'Confirm Password Should not be Empty';
                }
                break;

            case 'sum':
                let numberRegex = /^[0-9]{0,2}$/;
                if (numberRegex.test(fieldValue)) {
                    fields[fieldName] = numberRegex.test(fieldValue) ? fieldValue : fields[fieldName];
                    if (this.state.number1 + this.state.number2 === Number(fieldValue)) {
                        errors[fieldName] = '';
                    } else {
                        errors[fieldName] = 'Invalid Sum';
                    }
                } else if (!fieldValue) {
                    fields[fieldName] = '';
                    errors[fieldName] = 'Required';
                }
                break;

            default: fields[fieldName] = fieldValue;
        }

        this.setState({ fields, errors }, () => this.checkValidityOfForm());
    }

    resetPassword = () => {
        let { fields } = this.state;
        let payload = {};
        payload.login_id = fields.login_id;
        payload.password = md5(fields.new_password);
        payload.login_id &&
            this.props.resetPasswordAction(payload);
    }

    render() {
        const title = "Reset Password";
        let { fields, errors } = this.state
        return (
            <div className="reset-password-container">
                <PageHeader
                    header={title}
                />
                <form className="change-password-form">

                    <FormControl className="full-width form-control-custom" error={errors.new_password}>
                        <InputLabel htmlFor="new-password">New Password</InputLabel>
                        <Input
                            id="new-password"
                            name="new_password"
                            type="password"
                            value={fields.new_password}
                            onChange={this.handleChange}
                            aria-describedby="new-password-text"
                            fullWidth={true}
                        />
                        <FormHelperText id="new-password-text">{errors.new_password}</FormHelperText>
                    </FormControl>
                    <br />

                    <FormControl className="full-width form-control-custom" error={errors.confirm_new_password}>
                        <InputLabel htmlFor="confirm-password">Confirm Password</InputLabel>
                        <Input
                            id="confirm-password"
                            name="confirm_new_password"
                            type="password"
                            value={fields.confirm_new_password}
                            onChange={this.handleChange}
                            aria-describedby="confirm-password-text"
                            fullWidth={true}
                        />
                        <FormHelperText id="confirm-password-text">{errors.confirm_new_password}</FormHelperText>
                    </FormControl>
                    <br />

                    <div className="number-container">
                        <FormControl className="full-width form-control-custom">
                            <InputLabel htmlFor="number-one"></InputLabel>
                            <Input
                                id="number-one"
                                // name="number1"
                                // type="number"
                                value={this.state.number1}
                                // onChange={this.handleChange}
                                aria-describedby="number-one-text"
                                fullWidth={true}
                                disabled={true}
                            />
                            <FormHelperText id="number-one-text"></FormHelperText>
                        </FormControl>

                        <div className="plus-symbol">+</div>

                        <FormControl className="full-width form-control-custom">
                            <InputLabel htmlFor="number-two"></InputLabel>
                            <Input
                                id="number-two"
                                // name="number2"
                                // type="number"
                                value={this.state.number2}
                                // onChange={this.handleChange}
                                aria-describedby="number-two-text"
                                fullWidth={true}
                                disabled={true}
                            />
                            <FormHelperText id="number-two-text"></FormHelperText>
                        </FormControl>

                        <div className="plus-symbol">=</div>

                        <FormControl className="full-width form-control-custom" error={errors.sum}>
                            <InputLabel htmlFor="sum">Result</InputLabel>
                            <Input
                                id="sum"
                                name="sum"
                                // type="text"
                                value={fields.sum}
                                maxLength="2"
                                onChange={this.handleChange}
                                aria-describedby="sum-text"
                                fullWidth={true}
                            />
                            <FormHelperText id="sum-text">{errors.sum}</FormHelperText>
                        </FormControl>
                    </div>

                    <div className="button-container">
                        <Button
                            label="Change Password"
                            disabled={!this.state.enableSubmit}
                            onClick={this.resetPassword}
                        />
                    </div>
                </form>
            </div>
        );
    }
}


const mapStateToProps = (state, ownProps) => {
    return {
        resetData: state.resetPasswordData.resetData
    }
}

const mapDispatchToProps = (dispatch) => {
    return bindActionCreators({ resetPasswordAction, resetPasswordData }, dispatch)
}

export default connect(mapStateToProps, mapDispatchToProps)(ResetPassword);
