import { RESET_PASSWORD_SUCCESS, RESET_DATA_RESET_PW } from "./constants";
import { showLoader, hideLoader } from "../../components/Loader/action";
import handleError from "../../errorHandler";
import { editSalesMember } from "../../api";
import { toast } from "react-toastify";

export function resetPasswordAction(data) {
    return (dispatch) => {
        dispatch(showLoader());
        editSalesMember(data)
            .then(response => {
                dispatch(hideLoader());
                dispatch(resetPasswordSuccess());
                toast.success("Password Changed!");
                // toast.success(response.data.message);
            })
            .catch(error => {
                handleError(error, dispatch);
            });
    }
}

export function resetPasswordSuccess() {
    return {
        type: RESET_PASSWORD_SUCCESS
    }
}

export function resetPasswordData() {
    return {
        type: RESET_DATA_RESET_PW
    }
}
